@import url('https://fonts.googleapis.com/css2?family=Patrick+Hand&display=swap');

.App{
  min-height: 100vh;
  height: 100%;
}

.container_body {
  margin: 15pm auto 0;
  padding: 15px;
  max-width: 1400px;
}

.header{
  padding: 15px;
  background-color: white;
  box-shadow: 0px 3px rgba(0, 0, 0, 0.2);
}

.header-container{
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 0 auto;
  margin: 0 auto;
  font-family: 'Patrick Hand';
}

.logo h2{
  font-size: 2em;
}

.inputs {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.search-input {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white;
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
  width: 300px;
  padding: 5px;
  border-radius: 5px;
  gap: 5px;
  font-family: 'Patrick Hand';
}

.icon {
  margin-left: 10px;
}

.search-input input {
  border: 0;
  background-color: transparent;
  outline: none;
  font-size: 18px;
  font-family: 'Patrick Hand';
}

.select-filter {
  width: 300px;
  background-color: white;
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
  width: 200px;
  padding: 5px;
  border-radius: 5px;
}


.select-filter select {
  padding: 5px;
  width: 100%;
  background-color: transparent;
  border: none;
  outline: none;
}

.countries {
  max-width: 1400px;
  padding: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 35px;
}

.country {
  background-color: white;
  width: 305px;
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  height: 370px;
  cursor: pointer;
}

.country .flag-container {
  height: 170px;
  width: 100%;
}

.country .flag-container img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.country .name {
  margin-bottom: 10px;
}

.country .values {
  color: gray;
}

.country .details {
  padding: 15px;
}

.country .details p {
  margin-bottom: 5px;
}

.country-details {
  margin: 15px auto 0;
  padding: 15px;
  max-width: 1400px;
  min-height: 100vh;
}

.back {
  background-color: white;
  border: 0;
  outline: 0;
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
  margin-bottom: 25px;
  width: 180px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  padding: 10px;
  font-weight: bold;
  cursor: pointer;
}

.country-details-body {
  margin: 15px auto 0;
  padding: 15px;
  max-width: 1400px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 40px;
}

.img-container {
  flex: 0.5;
}

.img-container img {
  width: 100%;
  object-fit: cover;
}

.info {
  flex: 0.5;
}

.info-container {
  display: flex;
  justify-content: center;
  align-items: center;
margin-bottom: 45px;
}

.info h2 {
  margin-bottom: 15px;
}

.info .values {
  color: gray;
}

.border-country {
  padding: 5px 10px;
  background-color: white;
  display: inline-block;
  margin: 10px;
  cursor: pointer;
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
}

@media (max-width: 800px) {
  .country-details-body {
    flex-direction: column;
  }

  .info-container {
    flex-direction: column;
    align-items: flex-start;
  }

  .left-info {
    margin-bottom: 20px;
  }
}

@media (max-width: 720px) {
  .inputs {
    flex-direction: column;
    margin-bottom: 15px;
  }

  .search-input {
    margin-bottom: 20px;
  }

  .logo {
    flex: 0.5;
    font-size: 17px;
  }
}






